.home-container{
    background-color: white;
    height: 100vh;
    display: grid;
    grid-template-columns: 2fr 3fr;
    .left-container{
        background-color: #1E1E1E;
        display: flex;
        justify-content: center;
        align-items: center;
        .item-container{
            border: 2px solid red;
           color: white;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            img{
                width: 10px;
            }
            button{
                cursor: pointer;
                height: 50px;
                width: 90%;
                border-radius: 50px;
                border: none;
                transition-duration: 1s;
                font-size: 20px;
                
            }
            button:hover{
                box-shadow: 2px 2px 6px gray;
            }
        }
    }
}